.home{
    margin-top: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    display: block;
}


.home-animation h1 {
    display: inline-block;
    position: relative;
    color: 
  #6f6f6f;
  font-weight: 1000;
}
  
  .home-animation h1:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: 
  #0400ff;
    transform-origin: bottom right;
    transition: transform 0.25s ease-in-out;
  }


  
  .home-animation h1:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }