

.about{
    display: flex;
    align-items: center;
    justify-content: center;
    justify-content: space-between;
    height: 130vh;
    margin: auto;
    gap: 80px;
}

.right-about h1{
    display: block;
}



.home-animation  span{
    display: inline-block;
    position: relative;
    color: 
  #6f6f6f;
  font-weight: 1000;
  margin-top: 20px;
}
  
.home-animation span:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: 
  #0400ff;
    transform-origin: bottom right;
    transition: transform 0.25s ease-in-out;
  }


  
.home-animation span:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }



  @media screen and (max-width: 480px){
    .About{
      padding: 0.5rem 1rem;
    }
  }