.skill{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: auto;
    justify-content: center;
    justify-content: space-between;
    gap: 100px;
    height: 95vh;
}

.left-skills h1{
    font-weight: bolder;
    font-size: 30px;
}


.toggle-skill{
  display: flex;
  align-items: center;
  flex-direction: row;
  /* border-radius: 1rem; */
  width: 11%;
  height: 30px;
  gap: 5px;
}

.toggle-skill>button{
  border-radius: 20px;
}



.toggle-skill>t-button1{
  /* width: 100%; */
  background-color: var(--orange);
  position: absolute;
}



.skill-animation  span{
    display: inline-block;
    position: relative;
    color: 
  #6f6f6f;
  font-weight: 1000;
  margin-top: 20px;
}
  
.skill-animation span:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: 
  #0400ff;
    transform-origin: bottom right;
    transition: transform 0.25s ease-in-out;
  }


  
.skill-animation span:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }


  body {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100vh;
    margin: 0;
    padding: 0;
  }
  
  /* scrolldown */
  
  .scrolldown {
    position: relative;
    z-index: 0;
    cursor: pointer;
    right: -70px;
  }
  
  .scrolldown__arrows {
    position: relative;
    top: 12px;
    width: 48px;
    height: 48px;
  }
  
  .scrolldown__arrow {
    background: url(https://demo.l2banners.ru/res/scrolldown/scrolldown.svg) no-repeat;
    width: 48px;
    height: 16px;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    transform: scale(0.3) translateY(-30px);
  }
  
  .scrolldown__arrow_1 {
    animation: scrolldown__arrow 3s ease-out infinite;
  }
  
  .scrolldown__arrow_2 {
    animation: scrolldown__arrow 3s ease-out 1s infinite;
  }
  
  .scrolldown__arrow_3 {
    animation: scrolldown__arrow 3s ease-out 2s infinite;
  }
  
  @keyframes scrolldown__arrow {
    33.3% {
      opacity: 1;
      transform: scale(1) translateY(0);
    }
    66.6% {
      opacity: 1;
      transform: scale(1) translateY(12px);
    }
    100% {
      opacity: 0;
      transform: scale(0.5) translateY(40px);
    }
  }
  