.footer{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: auto;
    height: 20vh;
    align-items: center;
}

.footer-animation  span{
    display: inline-block;
    position: relative;
    color: 
  #6f6f6f;
  font-weight: 1000;
  margin-top: 20px;
  }
  
  .footer-animation span:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: 
  #0400ff;
    transform-origin: bottom right;
    transition: transform 0.25s ease-in-out;
  }
  
  
  
  .footer-animation span:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }