.n-wrapper{
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10rem;
}

.n-left{
    flex: 1;
    align-items: center;
    display: flex;
    gap: 40px;
}

.n-name{
    font-weight: bolder;
}

.n-right{
    display: flex;
    align-items: center;
    font-weight: 400;
}

.n-list{
    flex: 10;
    font-weight: bold;
}

.n-list>ul{
    display: flex;
    gap: 34px;
    margin-right: 5rem;
    list-style-type: none;
    cursor: pointer;
}

.n-list > ul >li:hover{
    cursor: pointer;
    color: var(--orange);
}

.n-list li:hover{
    color: rgba(208, 0, 255, 0.712);
}

.n-button{
    flex: 1;
}

@media screen and (max-width: 480px){
    .n-list{
        display: none;
    }
    .n-button{
        display: none;
    }
}