.circle{
    width: 3rem;
    height: 3rem;
    background-color: rgb(255, 255, 255);
    border-radius: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
    color: black;
}

.circle::before{
    border-radius: 100%;
    content: '';
    background-image: linear-gradient(to bottom, #6cdef8 0%, rgb(240, 179, 67) 100%);
    top: -8px;
    left: -8px;
    bottom: -8px;
    right: -8px;
    position: absolute;
    z-index: -1;
    box-shadow: var(--boxShadow);
}



.IntroExp-animation  span{
    display: inline-block;
    position: relative;
    color: 
  #6f6f6f;
  font-weight: 1000;
  margin-top: 20px;
}
  
.IntroExp-animation span:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: 
  #0400ff;
    transform-origin: bottom right;
    transition: transform 0.25s ease-in-out;
  }


  
.IntroExp-animation span:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }