.intro{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 70vh;
}


.left h1{
    display: block;
    gap:  3px;
    margin: 8px;
}

@media screen and (max-width: 480px){
    .intro{
      display: block;
      flex-direction: column;
    }
  }