.contact{
    display: flex;
    flex-direction: row;
    /* justify-content: start; */
    /* background-color: blue; */
    gap: 6rem;
    align-items: center;
}

.left{
    display: block;
    flex-direction: column;
}

.left span{
    font-weight: 500;
    font-size: 14px;
    opacity: 0.5;
}


.socialicon{
    display: flex;
    flex-direction: row;
    gap: 20px;
    /* transform: scale(0.8); */
    float: left;
}





.right {
    display: flex;
    justify-content: center;
    position: relative;
    flex: 1;
    margin-top: 100px;
  }
  
  .right > form {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
  }
  
  .right .user {
    width: 20rem;
    height: 2rem;
    padding: 0.3em;
    outline: none;
    border: 2px solid var(--orange);
    border-radius: 8px;
    font-size: 16px;
  }
  
  textarea {
    height: 4rem !important;
  }
  
  .c-blur1 {
    top: 1rem;
    left: 8rem;
  }
  
  .contact-animation  span{
    display: inline-block;
    position: relative;
    color: 
  #6f6f6f;
  font-weight: 1000;
  
  }
  
  .contact-animation span:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: 
  #0400ff;
    transform-origin: bottom right;
    transition: transform 0.25s ease-in-out;
  }
  
  
  
  .contact-animation span:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }
  


@media screen and (max-width: 480px){
    .contact{
      display: block;
      flex-direction: column;
    }
  }